@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600&display=swap);

.group{position:relative;margin:45px 0}.group .form-input{background:none;background-color:#fff;color:gray;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid gray;margin:25px 0}.group .form-input:focus{outline:none;border-color:#2820c4c1}.group .form-input:focus~.form-input-label{top:-14px;font-size:12px;color:#000}.group input[type=password]{letter-spacing:.3em}.group .form-input-label{color:gray;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;transition:300ms ease all}.group .form-input-label.shrink{top:-14px;font-size:12px;color:#000}
.custom-button {
    min-width: 200px;
    border-radius: 30px;
    text-align: center;
    padding: 15px 0px;
    box-shadow: 1px 1px 20px 7px #665dfe4f;
    color: rgba(26, 27, 29, 0.933);
    transition: 0.3s ease-in;
    transition: 0.4s ease;
}

.custom-button:hover {
    background-color: #665dfe;
    color: #edeeee;
}


.sign-up,
.sign-in {
    max-width: 500px;
    margin: auto;
}
.sign-up {
    display: flex;
    flex-direction: column;
}
.sign-up .title {
    margin: 10px 0;
    font-size: 2rem;
}

.sign-up .buttons {
    display: flex;
    justify-content: space-between;
}

.auth-page-btn-c {
    width: 100%;
    text-align: center;
    padding: 50px 0px;
}
.btn-group {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: auto;
}
.auth-btn {
    min-width: 150px;
    text-align: center;
    padding: 15px 0px;
    background-color: #665dfe1c;
    box-shadow: 1px 1px 20px 7px #665dfe4f;
    color: rgba(26, 27, 29, 0.933);
    transition: 0.3s ease-in;
}
.auth-btn-l {
    border-radius: 250px 0px 0px 25px;
}
.auth-btn-r {
    border-radius: 0px 25px 250px 0px;
}
.auth-btn-active {
    background-color: #665dfe;
    color: #edeeee;
    box-shadow: -1px 14px 46px 1px #665dfe;
}
.auth-btn-active:hover {
    color: #dadada;
}

.message-input {
    border-radius: 30px;
    width: 90%;
    padding: 10px 20px;
    left: 0;
    border-color: transparent;
    background-color: transparent;
}
.message-input:focus {
    padding: 10px 20px;
    left: 0;
    border-color: transparent;
    outline: none;
    background-color: #ffff;
}
.font-weight-semibold {
    font-size: 48px;
}

.main-visible {
  justify-content: center;
  align-items: center;
}

.list-username{
    font-size: 12px;
}
.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        -webkit-animation: App-logo-spin infinite 20s linear;
                animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

